import * as React from 'react';
import Box from '@mui/material/Box';
import LayerControl from './LayerControl';

export const getLayerState=(id)=>{
    const val=localStorage.getItem(id);
    if(!val){
        if(id.indexOf("dis")>=0){
            return false;
        }else{
            return true;
        }
    }
    return JSON.parse(val);
};

export default function MenuList() {
    return (
        <div>
        <Box sx={{ lineHeight: 1,fontSize: 'small' }}>
        <LayerControl width={180} checked={getLayerState("bldg")} title={"LOD"} layerId={["bldg","bridge"]} opacity={90} type={'fill-extrusion-opacity'} />
        <LayerControl width={180} checked={getLayerState("bldg-lod0")} title={"MVT"} layerId={["bldg-lod0"]} opacity={90} type={'fill-extrusion-opacity'} />
        <LayerControl width={180} checked={getLayerState("mvt-road")} title={""} layerId={["mvt-road"]} opacity={70} type={'fill-opacity'} />
        <LayerControl width={180} checked={getLayerState("vector-road")} title={""} layerId={["vector-road"]} opacity={100} type={'line-opacity'} />
        <LayerControl width={180} checked={getLayerState("vector-rail")} title={""} layerId={["vector-rail"]} opacity={80} type={'line-opacity'} />
        <LayerControl width={180} checked={getLayerState("vector-water")} title={""} layerId={["vector-water"]} opacity={25} type={'fill-opacity'} />
        <LayerControl width={180} checked={getLayerState("mvt-pad")} title={""} layerId={["mvt-pad"]} opacity={40} type={'fill-opacity'} />
        <LayerControl width={180} checked={getLayerState("dis-land")} title={""} layerId={["dis-land"]} opacity={40} type={'fill-opacity'} />
        <LayerControl width={180} checked={getLayerState("dis-tsunami")} title={""} layerId={["dis-tsunami"]} opacity={40} type={'fill-opacity'} />
        </Box>
        </div>
    );
};